import {
  Component,
  ViewChild,
  ElementRef,
  OnInit,
  OnDestroy,
  Output,
  EventEmitter,
} from '@angular/core';
import { UploadActions } from '../actions/upload.actions';
import {
  faTimes,
  faSave,
  faTrash,
} from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-take-photo',
  templateUrl: './takephoto.component.html',
  styleUrls: ['./takephoto.component.css']
})
export class TakePhotoComponent implements OnInit, OnDestroy {
  @ViewChild('videoComponent')
  videoComponent: ElementRef;
  @ViewChild('canvasComponent')
  canvasComponent: ElementRef;
  @ViewChild('videoWrapper')
  videoWrapper: ElementRef;
  @Output() fileChange = new EventEmitter<any>();
  @Output() manageTakePhoto = new EventEmitter<boolean>();
  canvasContext;
  showPhoto;
  faTimes = faTimes;
  faSave = faSave;
  faTrash = faTrash;
  stream: any;
  isPhotoSrc = false;
  constructor (private uploadActions: UploadActions) {}
  public ngOnInit(): void {
    this.canvasContext = this.canvasComponent.nativeElement.getContext('2d');
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices.getUserMedia({ video: true }).then((stream: any) => {
        this.stream = stream;
        this.videoComponent.nativeElement.src = window.URL.createObjectURL(stream);
        this.videoComponent.nativeElement.play();
      });
    }
  }
  public ngOnDestroy(): void {
    this.videoComponent.nativeElement.pause();
  }
  public takePhoto (): void {
    const { videoHeight, videoWidth } = this.videoComponent.nativeElement;
    const percentWidth = (640 * 100) / videoWidth * 0.01;
    const newImageHeight = percentWidth * videoHeight;
    this.canvasComponent.nativeElement.height =  newImageHeight;
    this.videoWrapper.nativeElement.style.height = `${newImageHeight}px`;
    this.canvasContext.drawImage(this.videoComponent.nativeElement, 0, 0, 640, newImageHeight);
    this.showPhoto = setTimeout(() => {  this.isPhotoSrc = true; }, 100);
  }
  public clearPhoto(): void {
    this.isPhotoSrc = false;
    const { width, height } = this.videoComponent.nativeElement;
    this.canvasContext.clearRect(0, 0, width, height);
  }
  public savePhoto(): void {
    const url = this.canvasComponent.nativeElement.toDataURL();
    const readyToSaveData = atob(url.split(',')[1]);
    const fileBuffer = [];
    for (let i = 0; i < readyToSaveData.length; i++) {
      fileBuffer.push(readyToSaveData.charCodeAt(i));
    }
    const uintArray = new Uint8Array(fileBuffer);
    const file = new Blob([uintArray], {type: 'image/jpeg'});
    this.closeMenu();
    this.fileChange.emit({
      target: {
        files: [file]
      }
    });
  }
  public closeMenu(): void {
    this.stream.getTracks().forEach(track => track.stop());
    this.manageTakePhoto.emit(false);
  }

}
