import { AnyAction } from 'redux';
import { AddToFavoriteActions } from '../../actions/add.tofavorite.actions';

export interface FavoritesModel {
  favorites: string[];
}

export const favoritesInitState: FavoritesModel =  {
  favorites: [],
};

export function favoritesState(state: FavoritesModel = favoritesInitState, action: AnyAction ): FavoritesModel {
  return {
    [AddToFavoriteActions.MANAGE_FAVORITE_ACTION_SUCCESS]: {
      favorites: action.favorites,
    },
    'default': state,
  }[AddToFavoriteActions[action.type] || 'default'];
}
