import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef,
  EventEmitter,
  Output,
  OnChanges
} from '@angular/core';
import { NgRedux } from '@angular-redux/store';
import { CatImage } from '../store/reducers/cats.images';
import { RootReducerModel } from '../store';
import { UploadActions } from '../actions/upload.actions';
import { GetImagesActions } from '../actions/get.images.actions';
import { AddToFavoriteActions } from '../actions/add.tofavorite.actions';
import {
  faTrash,
  faSpinner,
  faHeart,
} from '@fortawesome/free-solid-svg-icons';
@Component({
  selector: 'app-images',
  templateUrl: './images.html',
  styleUrls: ['./images.component.css']
})
export class ImagesComponent implements OnDestroy, OnInit, OnChanges {
  @ViewChild('imagesWrapper')
  imagesWrapper: ElementRef;
  @ViewChild('modal')
  modal: ElementRef;
  @Output() fileChange = new EventEmitter<any>();
  @Output() manageTakePhoto = new EventEmitter<boolean>();
  images: CatImage[];
  favorites: string[] = [];
  subscriptionImages;
  subscriptionLoading;
  subscriptionFavorites;
  subscribeIsFavorite;
  isLoading = false;
  imagePage = 0;
  imageActive;
  imageId = '';
  isFavorite = false;
  faTrash = faTrash;
  faSpinner = faSpinner;
  faHeart = faHeart;
  constructor(
    private ngRedux: NgRedux<RootReducerModel>,
    private uploadActions: UploadActions,
    private getImagesAgtions: GetImagesActions,
    private manageFaforite: AddToFavoriteActions,
  ) {
    this.subscriptionImages = ngRedux.select<CatImage[]>(['catsImagesState', 'catsImages'])
      .subscribe(data => this.images = data);
    this.subscriptionLoading = ngRedux.select<boolean>(['catsImagesState', 'loading'])
      .subscribe(data => {
        this.isLoading = data;
      });
    this.subscriptionFavorites = ngRedux.select<string[]>(['favoritesState', 'favorites'])
      .subscribe(data => {
        this.favorites = data;
      });
    this.subscribeIsFavorite = ngRedux.select<string[]>(['favoritesState', 'favorites'])
      .subscribe(data => {
        if (data && data.some) {
          this.isFavorite = data.some(el => el === this.imageId);
        }
      });
  }
  public selectImg(select: string, imageId: string): void {
    if (!select) {
      this.isFavorite = false;
    }
    this.imageActive = select;
    this.imageId = imageId;
    this.modal.nativeElement.style.display = select ? 'block' : 'none';
    if (select && this.favorites) {
      this.isFavorite =  this.favorites.some(el => el === this.imageId);
    }
  }
  public ngOnInit(): void {
    this.getImages();
    this.manageFaforite.getFavorites();
    this.imagesWrapper.nativeElement.style.height = window.innerHeight - 29 + 'px';
  }
  public ngOnDestroy(): void {
    this.subscriptionImages.unsubscribe();
    this.subscriptionLoading.unsubscribe();
    this.subscriptionFavorites.unsubscribe();
    this.subscribeIsFavorite.unsubscribe();
  }
  public ngOnChanges(): void {
    console.dir('ngOnChanges');
  }
  public filesChange(event) {
    this.fileChange.emit(event);
  }
  public scrollContent(event): void {
    const { scrollHeight, scrollTop, clientHeight } = event.target;
    const isCloseToBottom = scrollHeight - scrollTop === clientHeight;
    if (isCloseToBottom) {
      this.getImages();
    }
  }
  public markAsFavorite(): void {
    this.manageFaforite.manageFavorite(this.imageId);
  }
  private getImages(): void {
    this.getImagesAgtions.getImages(this.imagePage);
    this.imagePage++;
  }
  public openTakePhoto(): void {
    this.manageTakePhoto.emit(true);
  }
}
