import { Injectable } from '@angular/core';
import { AnyAction } from 'redux';
import { dispatch } from '@angular-redux/store';
import {of} from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import { map, catchError, } from 'rxjs/operators';
import { ofType } from 'redux-observable';
import { getDataAction } from '../api';

export const getImageEpic = action$ => action$.pipe(
  ofType(GetImagesActions.GET_IMAGES_REQUEST),
  mergeMap((action: AnyAction) => getDataAction(`https://api.thecatapi.com/v1/images/?limit=20&page=${action.pageNumber}&order=DESC`)),
  map((response: any) => ({type: GetImagesActions.GET_IMAGES_SUCCESS, images: response.response}))
);
@Injectable()
export class GetImagesActions {
  static GET_IMAGES_REQUEST = 'GET_IMAGES_REQUEST';
  static GET_IMAGES_SUCCESS = 'GET_IMAGES_SUCCESS';
  static GET_IMAGES_ERROR = 'GET_IMAGES_ERROR';
  @dispatch()
  getImages(pageNumber: number): AnyAction {
    return { type: GetImagesActions.GET_IMAGES_REQUEST, pageNumber};
  }
}
