import { ajax } from 'rxjs/ajax';
import { Subscriber } from 'rxjs';
import { map } from 'rxjs/operators';
import { dispatch } from '@angular-redux/store';

class ClassLD {
  @dispatch()
  public progress() {
    return {
      type: 'sadas',
    };
  }
}
const v = new ClassLD();

function dataAction(method: string, url: string, params: any = null, headers: object = {}) {
  const progressSubscriber = new Subscriber(progressEvent => { v.progress(); });
  return ajax({
    method: method,
    url: url,
    body: method === 'put' || method === 'post' || method === 'path' || method === 'patch' ? params : null,
    headers: Object.assign({}, headers, {'x-api-key': '9800a6db-8633-4af5-82b1-d9c983ba5e25'}),
    progressSubscriber: progressSubscriber,
  });
}


export function getDataAction(url: string, params: any = null, headers: object = {}) {
  return dataAction('get', url, params, headers);
}

export function postDataActions(url: string, params: any = null, headers: object = {}) {
  return dataAction('post', url, params, headers);
}
