export function catsImagesState(state: CatsImagesState = initialCatsImagesState, action: AnyAction): CatsImagesState {
   return {
     [UploadActions.UPLOAD_FILE_REQUEST]: {
       loading: true,
       catsImages: state.catsImages.map(el => ({...el})),
       error: false,
    },
     [UploadActions.UPLOAD_FILE_SUCCESS]: {
       loading: false,
       catsImages: [action.image, ...state.catsImages.map(el => ({...el}))],
       error: false,
     },
     [UploadActions.UPLOAD_FILE_ERROR]: {
       loading: false,
       catsImages: state.catsImages.map(el => ({...el})),
       error: true,
     },
     [GetImagesActions.GET_IMAGES_REQUEST]: {
       loading: true,
       catsImages: state.catsImages.map(el => ({...el})),
       error: false,
     },
     [GetImagesActions.GET_IMAGES_SUCCESS]: {
       loading: false,
       catsImages: [...state.catsImages.map(el => ({...el})), ...action.images],
       error: false,
     },
     [GetImagesActions.GET_IMAGES_ERROR]: {
       loading: false,
       catsImages: state.catsImages.map(el => ({...el})),
       error: true,
     },
     'default': state,
  }[GetImagesActions[action.type] || UploadActions[action.type] || 'default'];
}

function cats(state: CatsImagesState = initialCatsImagesState, action: AnyAction): CatsImagesState{
  switch (action.type) {
    case UploadActions.UPLOAD_FILE_REQUEST:
      return {
        loading: true,
        catsImages: state.catsImages.map(el => ({...el})),
        error: false,
      };
    case UploadActions.UPLOAD_FILE_SUCCESS:
      return {
        loading: false,
        catsImages: [...state.catsImages.map(el => ({...el})), action.image],
        error: false,
      };
    case UploadActions.UPLOAD_FILE_ERROR:
      return {
        loading: false,
        catsImages: state.catsImages.map(el => ({...el})),
        error: true,
      };
    case GetImagesActions.GET_IMAGES_REQUEST:
      return {
        loading: true,
        catsImages: state.catsImages.map(el => ({...el})),
        error: false,
      };
    case GetImagesActions.GET_IMAGES_SUCCESS:
      return {
        loading: false,
        catsImages: [...state.catsImages.map(el => ({...el})), ...action.images],
        error: false,
      };
    case GetImagesActions.GET_IMAGES_ERROR:
      return {
        loading: false,
        catsImages: state.catsImages.map(el => ({...el})),
        error: true,
      };
    default:
      return state;
  }
}

