import { Injectable } from '@angular/core';
import { AnyAction } from 'redux';
import { dispatch } from '@angular-redux/store';
import { ofType} from 'redux-observable';
import { mergeMap, map,  } from 'rxjs/operators';
import {of} from 'rxjs';

export const manageFavoriteEpic = action$ => action$.pipe(
  ofType(AddToFavoriteActions.MANAGE_FAVORITE_ACTION),
  mergeMap((action: AnyAction) => {
    const favorites = localStorage.getItem('favorites');
    if (favorites) {
      const favoritesToJS = JSON.parse(favorites);
      const isAlreadyFavorite = favoritesToJS.some(el => el === action.contentId);
      if (isAlreadyFavorite) {
        const filteredFavorites = favoritesToJS.filter(el => el !== action.contentId);
        localStorage.setItem('favorites', JSON.stringify(filteredFavorites));
        return of(filteredFavorites);
      }
      favoritesToJS.push(action.contentId);
      localStorage.setItem('favorites', JSON.stringify(favoritesToJS));
      return of(favoritesToJS);
    }
    localStorage.setItem('favorites', JSON.stringify([action.contentId]));
    return of([action.contentId]);
  }),
  map(response => {
    return {type: AddToFavoriteActions.MANAGE_FAVORITE_ACTION_SUCCESS, favorites: response};
  })
);

export const getFavoriteEpic = action$ => action$.pipe(
  ofType(AddToFavoriteActions.GET_FAVORITES),
  map(() => {
    const favorites = localStorage.getItem('favorites');
    const favoritesToJS = favorites ? JSON.parse(favorites) : [];
    return {
      type: AddToFavoriteActions.MANAGE_FAVORITE_ACTION_SUCCESS,
      favorites: favoritesToJS,
    };
  })
);

@Injectable()
export class AddToFavoriteActions {
  static MANAGE_FAVORITE_ACTION = 'MANAGE_FAVORITE_ACTION';
  static MANAGE_FAVORITE_ACTION_SUCCESS = 'MANAGE_FAVORITE_ACTION_SUCCESS';
  static GET_FAVORITES = 'GET_FAVORITES';
  @dispatch()
  manageFavorite(contentId: string): AnyAction {
    return {type: AddToFavoriteActions.MANAGE_FAVORITE_ACTION, contentId };
  }
  @dispatch()
  getFavorites(): AnyAction {
    return { type: AddToFavoriteActions.GET_FAVORITES };
  }
}
