import { Injectable } from '@angular/core';
import { AnyAction } from 'redux';
import { dispatch } from '@angular-redux/store';
import {of} from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import { map, catchError, } from 'rxjs/operators';
import { ofType } from 'redux-observable';
import { postDataActions } from '../api';

export const epicUpload = action$ => action$.pipe(
  ofType(UploadActions.UPLOAD_FILE_REQUEST),
  mergeMap((action: AnyAction) => {
    const file = new FormData();
    file.append('file', action.file);
    return postDataActions('https://api.thecatapi.com/v1/images/upload', file, {'Access-Control-Allow-Origin': '*'});
  }),
  map((response: any) => {
    console.log(response);
    return { type: UploadActions.UPLOAD_FILE_SUCCESS, image: response.response};
  }),
  catchError(val => of(`I caught: ${val}`))
);

@Injectable()
export class UploadActions {
  static UPLOAD_FILE_REQUEST = 'UPLOAD_FILE_REQUEST';
  static UPLOAD_FILE_SUCCESS = 'UPLOAD_FILE_SUCCESS';
  static UPLOAD_FILE_ERROR = 'UPLOAD_FILE_ERROR';
  @dispatch()
  uploadFile(file): AnyAction {
    return { type: UploadActions.UPLOAD_FILE_REQUEST, file };
  }
}
