import { AnyAction } from 'redux';
import { RootReducerModel } from './models/RootReducerModel';
import { catsImagesState, initialCatsImagesState } from './reducers/cats.images';
import { favoritesInitState, favoritesState } from './reducers/favorites';

export function rootReducer(state: RootReducerModel, action: AnyAction): RootReducerModel {
  console.dir(action);
  return {
    favoritesState: favoritesState(state.favoritesState, action),
    catsImagesState: catsImagesState(state.catsImagesState, action),
  };
}

export const initialState: RootReducerModel = {
  favoritesState: favoritesInitState,
  catsImagesState: initialCatsImagesState,
};

export {
  RootReducerModel
};
