import { Component } from '@angular/core';
import { NgRedux } from '@angular-redux/store';
import { TodoActions } from './actions/tasks.actions';
import { RootReducerModel } from './store';
import { CatImage } from './store/reducers/cats.images';
import { UploadActions } from './actions/upload.actions';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  catImages: CatImage[];
  takePhoto = false;
  subscriptionImages;
  constructor(
    private ngRedux: NgRedux<RootReducerModel>,
    private actions: TodoActions,
    private uploadActions: UploadActions) {
    this.subscriptionImages = ngRedux.select<CatImage[]>(['catsImagesState', 'catsImages'])
      .subscribe(data => this.catImages = data );
  }
  public manageModal(takePhoto: boolean) {
    this.takePhoto = takePhoto;
  }
  public fileChange(event) {
    Array.prototype.forEach.call(event.target.files, el => {
      this.uploadActions.uploadFile(el);
    });
  }
}
